import { DlwBaseWebComponent } from "./base/dlw-webcomponent-base";
import { observeMediaQuery } from "./utils/media-utils";
import { getMatchMedia } from "./utils/media-utils";
import queryFocusable from "ally.js/src/query/focusable";

export class DlwFocusTrap extends DlwBaseWebComponent {
    focusableEls;

    //** Turn off ShadowDOM */
    createRenderRoot() {
        return this;
    }

    init() {
        const ignoreFocus = Array.from(this.querySelectorAll("[data-focus-trap-ignore]"));

        ignoreFocus.map((element) => {
            const attributeValue = element.getAttribute("data-focus-trap-ignore");
            const mqIgnoreFocus = getMatchMedia(element, element.getAttribute("data-focus-trap-ignore"));
            if (mqIgnoreFocus) {
                observeMediaQuery(
                    attributeValue,
                    (matches) => {
                        if (matches) {
                            element.setAttribute("inert", true);
                        } else {
                            element.removeAttribute("inert");
                        }
                    },
                    document.window,
                );
            } else {
                element.setAttribute("inert", true);
            }
        });

        const focusTrap = this.getAttribute("media");

        this.getAllFocusableEl();

        let mqFocusTrap = getMatchMedia(this, focusTrap);

        if (mqFocusTrap) {
            observeMediaQuery(
                focusTrap,
                (matches) => {
                    if (matches) {
                        this.addEventListener("keydown", this.listener);
                    } else {
                        this.removeEventListener("keydown", this.listener);
                    }
                },
                document.window,
            );
        } else {
            this.addEventListener("keydown", this.listener);
        }

        let mObserver = new MutationObserver((entries) => {
            entries.forEach((entry) => {
                if (entry.oldValue !== null) {
                    entry.target.querySelector('[slot = "trigger"]')?.focus();
                }
            });
            this.getAllFocusableEl();
        });

        mObserver.observe(document, {
            attributeFilter: ["open"],
            attributeOldValue: true,
            subtree: true,
        });
    }

    getAllFocusableEl() {
        this.focusableEls = queryFocusable({
            context: this,
            includeContext: true,
            strategy: "quick",
        });
    }

    listener(e) {
        this.getAllFocusableEl();
        var firstFocusableEl = this.focusableEls[0];
        var lastFocusableEl = this.focusableEls[this.focusableEls.length - 1];

        this.focusTrap(e, firstFocusableEl, lastFocusableEl);
    }

    focusTrap(e, firstFocusableEl, lastFocusableEl) {
        if (e.key === "Tab") {
            /* shift + tab */
            if (e.shiftKey) {
                if (document.activeElement === firstFocusableEl) {
                    lastFocusableEl.focus();
                    e.preventDefault();
                }
            } /* tab */ else {
                if (document.activeElement === lastFocusableEl) {
                    firstFocusableEl.focus();
                    e.preventDefault();
                }
            }
        }
    }
}

DlwFocusTrap.define("dlw-focus-trap");
